<template>
    <div>
        <div ref="demo" >
            <br/>
            <basic-list
                :fields="fields"
                :list="list"
                :action-render="actionRender"
                />
            <br />
            <h4></h4>
            <br />
            <p>1. el-table + sortablejsrow-key</p>
            <br />
            <basic-list
                ref="selList"
                row-key="field"
                :fields="fields"
                :list="selList"
            />
        </div>
        <div v-if="!isPrint">
            <el-button size="mini" @click="handleIframePrint">iframe</el-button>
            <el-button size="mini" @click="handlePrint"></el-button>
            <el-button size="mini" @click="handleOpenPrint"></el-button>
        </div>
    </div>
</template>

<script>
    import Sortable from 'sortablejs'
    import BasicList from '@/components/basic-list'
    import { mapState } from 'vuex'
    export default {
        components: {
            BasicList
        },
        data() {
            return {
                fields: [
                    { key: 'name', name: '' },
                    { key: 'field', name: '' }
                ],
                list: [
                    {field: 'goodsNo', name: ''},
                    {field: 'goodsName', name: ''},
                    {field: 'qty', name: ''},
                    {field: 'price', name: ''},
                    {field: 'easyName', name: ''},
                ],
                selList: [],
            }
        },
        computed: {
            ...mapState('app', ['isPrint'])
        },
        mounted() {
            this.$nextTick(() => {
                this.initSortable()
            })
        },
        methods: {
            actionRender(h, {row}) {
                return <el-button type="text" size="mini" on-click={() => this.handleAdd(row)}></el-button>
            },
            handleAdd(row) {
                this.selList.findIndex(item => item.field === row.field) === -1 && this.selList.push(row)
            },
            initSortable() {
                let el = this.$refs.selList.$el.querySelector('tbody')
                new Sortable(el, {
                    sort: true,
                    draggable: '.el-table__row',
                    onEnd: ({newIndex, oldIndex}) => {
                        this.selList.splice(newIndex, 0, this.selList.splice(oldIndex, 1)[0])
                        // console.table(this.selList)
                    }
                })
            },
            // 
            handleIframePrint() {
                let iframe = document.createElement('IFRAME')
                iframe.setAttribute('style', 'width: 1200px; height: 0; overflow: scroll')
                document.body.appendChild(iframe)
                iframe.contentDocument.body.appendChild(this.$refs.demo)
                iframe.contentWindow.print()
                location.reload()
            },
            handlePrint() {
                new Promise((resolve, reject) => {
                    this.$store.commit('app/updatePrint', true)
                    resolve()
                }).then(() => {
                    window.print()
                }).then(() => {
                    this.$store.commit('app/updatePrint', false)
                })
            },
            handleOpenPrint() {
                let html = this.$refs.demo.outerHTML
                let style = `<style>
                        table {text-align: center}
                </style>`
                let newWin = window.open("")
                newWin.document.write(html+style)
                newWin.print()
                newWin.close()
            }
        }
    }
</script>

<style lang="scss" scoped>

</style>
